.MainMenu {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #14240b;
  padding: 20px;
  margin: 0 20px;
  min-height: 100vh;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.components {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Две колонки */
  gap: 20px;
  justify-content: center;
  width: 100%;
  max-width: 100%; /* Ограничение ширины */
  padding: 0 10px; /* Отступы по бокам */
  box-sizing: border-box;
}

.component {
  background-color: #253d26;
  border: 3px solid #99cf9a;
  border-radius: 10px;
  padding: 10px; /* Уменьшение отступов */
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.component:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.component img {
  max-width: 100%;
  max-height: 250px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
  border: 2px solid #99cf9a;
}

.component h2 {
  color: #acc329;
  margin: 10px 0;
  font-size: 1.2em;
}

.quantity-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
}

.quantity-controls button {
  background-color: #0d9453;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
}

.quantity-controls button:hover {
  background-color: #0d9453;
  transform: scale(1.05);
}

.quantity-controls input {
  margin: 0 10px;
  padding: 5px;
  font-size: 1em;
  border-radius: 5px;
  border: 1px solid #ddd;
  width: 60px;
  text-align: center;
}

.component button {
  background-color: #0d9453;
  color: #fff;
  border: none;
  padding: 7px 7px; /* Увеличенный размер кнопки */
  border-radius: 7px;
  cursor: pointer;
  font-size: 1.2em; /* Увеличенный размер текста */
  transition: background-color 0.2s, transform 0.2s;
}

.component button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

/* Адаптивные стили для мобильных устройств */
@media (max-width: 768px) {
  .components {
    grid-template-columns: repeat(2, 1fr); /* Две колонки на мобильных устройствах */
  }
}
